<template>
  <div class="hello">
    <el-row>
      <el-col :span="24" class="elm" v-inview:style="{background: '#1D8CE0'}"
        v-inview:style.leave="{background: '#20A0FF'}">
        <div class="box-inview">
          <h1>VUE INVIEW</h1>
          <p>Viewport, get notified when element enter or leave.</p>
          <p>
            <el-button @click="goNpm">NPM Package</el-button>
            <el-button @click="goGithub">Github Repo.</el-button>
          </p>
        </div>
      </el-col>
      <el-col :span="24" class="elm" v-inview:style="{background: '#1D8CE0'}"
        v-inview:style.leave="{background: '#20A0FF'}">
        <div class="inner-container">
          <h2># Instalation</h2>
          <pre style="background: #eee">
            <code style="color: #222">
              // Install from npm package
              npm install --save vueinview
              // install from github repository
              npm install --save rachmanzz/vueinview
            </code>
          </pre>
          <h2># Usage</h2>
          <h3>- Vue Instance</h3>
          <pre style="background: #eee">
            <code style="color: #222">
              import Vue from 'vue'
              import Inview from 'vueinview'

              Vue.use(Inview)
            </code>
          </pre>
        </div>
        </el-col>
        <el-col :span="24" class="elm" v-inview:style="{background: '#1D8CE0'}"
        v-inview:style.leave="{background: '#20A0FF'}">
        <div class="inner-container">
          <h3>- Threshold & Offset</h3>
          <p>Threshold <a href="https://github.com/camwiegert/in-view#inviewthresholdthreshold">check here</a>,
          Offset <a href="https://github.com/camwiegert/in-view#inviewoffsetoffset">check here</a>
          </p>
          <pre style="background: #eee">
            <code style="color: #222">
              import Vue from 'vue'
              import Inview from 'vueinview'

              Vue.use(Inview)
              Inview.threshold(/* threshold */)
              Inview.offset(/* offset */)
            </code>
          </pre>
          <h3>- Vue Directive</h3>
          <pre style="background: #eee">
            <samp style="color: #222">
              <span><</span>element v-inview:on="methods"<span>></span><span><</span>/element<span>></span>
              <span><</span>div v-inview:enter="methods"<span>></span><span><</span>/div<span>></span>
            </samp>
          </pre>
          <p>More documentation see <a href="https://github.com/rachmanzz/vue-inview/#vue-directive">here</a></p>
        </div>
        </el-col>
        <el-col :span="24" class="elm" v-inview:style="{background: '#1D8CE0'}"
        v-inview:style.leave="{background: '#20A0FF'}">
          <div class="inner-container">
            <h3># Example</h3>
            <h4>- Add & Remove Style</h4>
            <pre style="background: #eee">
              <samp style="color: #222">
                <span><</span>div v-inview:style="{background: '#222'}" v-inview:style.leave="['background']"<span>></span>
                  <span><</span>p<span>></span>lorem ipsum lorem ipsum lorem ipsum lorem ipsum lorem ipsum lorem ipsum lorem ipsum 
                  lorem ipsum lorem ipsum lorem ipsum lorem ipsum lorem ipsum<span><</span>/p<span>></span>
                <span><</span>/div<span>></span>
              </samp>
            </pre>
            <br>
            <el-button @click="trying(1)">Try Source</el-button>
            <div v-inview:style="{background: '#222'}" v-inview:style.leave="['background']" v-show="onstyling">
                <p>lorem ipsum lorem ipsum lorem ipsum lorem ipsum lorem ipsum lorem ipsum lorem ipsum 
                lorem ipsum lorem ipsum lorem ipsum lorem ipsum lorem ipsum
                lorem ipsum lorem ipsum lorem ipsum lorem ipsum lorem ipsum lorem ipsum lorem ipsum 
                lorem ipsum lorem ipsum lorem ipsum lorem ipsum lorem ipsum
                lorem ipsum lorem ipsum lorem ipsum lorem ipsum lorem ipsum lorem ipsum lorem ipsum 
                lorem ipsum lorem ipsum lorem ipsum lorem ipsum lorem ipsum
                lorem ipsum lorem ipsum lorem ipsum lorem ipsum lorem ipsum lorem ipsum lorem ipsum 
                lorem ipsum lorem ipsum lorem ipsum lorem ipsum lorem ipsum
                lorem ipsum lorem ipsum lorem ipsum lorem ipsum lorem ipsum lorem ipsum lorem ipsum 
                lorem ipsum lorem ipsum lorem ipsum lorem ipsum lorem ipsum
                lorem ipsum lorem ipsum lorem ipsum lorem ipsum lorem ipsum
                lorem ipsum lorem ipsum lorem ipsum lorem ipsum lorem ipsum lorem ipsum lorem ipsum 
                lorem ipsum lorem ipsum lorem ipsum lorem ipsum lorem ipsum
                lorem ipsum lorem ipsum lorem ipsum lorem ipsum lorem ipsum
                lorem ipsum lorem ipsum lorem ipsum lorem ipsum lorem ipsum lorem ipsum lorem ipsum 
                lorem ipsum lorem ipsum lorem ipsum lorem ipsum lorem ipsum
                </p>
            </div>
            <h5 v-show="onstyling"> ** Try to scroll up and scroll down again</h5>
          </div>
        </el-col>
        <el-col :span="24" class="elm" v-inview:style="{background: '#1D8CE0'}"
        v-inview:style.leave="{background: '#20A0FF'}">
          <div class="inner-container">
            <h4>- On element entered</h4>
            <pre style="background: #eee">
              <code style="color: #222">
                ## on element
                <span><</span>div v-inview:enter="seeing"<span>></span>
                <span><</span>p<span>></span>Yes, I'am See you..<span><</span>/p<span>></span>
                <span><</span>/div<span>></span>

                ## on methods vue instance
                ...
                 methods: {
                   seeing () {
                     alert('Are you see me ??')
                   }
                 }
                ...

              </code>
            </pre>
            <el-button @click="trying(6)">Try Source</el-button>
            <div v-inview:enter="seing" v-show="onseen" style="background: #eee; color: #222; margin-top: 10px; padding: 10px">
              <p>Yes, I'am See you..</p>
              <p>Try to scroll up and scroll down again</p>
            </div>
          </div>
        </el-col>
        <el-col :span="24" class="elm" v-inview:style="{background: '#1D8CE0'}"
        v-inview:style.leave="{background: '#20A0FF'}">
          <div class="inner-container">

            <h1 style="text-align: center; margin-top: 20%">Other Example Available Next Time</h1>
          </div>
        </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: 'hello',
  data () {
    return {
      onseen: false,
      onstyling: false
    }
  },
  methods: {
    goNpm () {
      window.location = 'https://npmjs.com/package/vueinview'
    },
    goGithub () {
      window.location = 'https://github.com/rachmanzz/vue-inview'
    },
    seing () {
      alert('Are you see me ??')
    },
    trying (num) {
      if (num === 1) {
        this.onstyling = true
      }
      if (num === 6) {
        this.onseen = true
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .box-inview{
    margin-top: 15%;
    text-align: center;
    color: #fff;
  }
  .inner-container{
    width: 80%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 7%;
    color: #fff;
  }
</style>



